import uniCountdown from "../../components/uni-countdown/uni-countdown.vue"
import {
	String2Ab,
	Ab2String
} from '../../utils/index.js'
//状态管理
import {
	mapMutations,
	mapState
} from 'vuex'

export default {
	data() {
		return {
			power: 0,
			batteryTimmer: 0,
			//倒计时时间
			countDownTime: 5,
			//重置组件标识
			hackReset: true,
			countdownColor: '#00B26A',
			canTap: false,
			timmer1: null,
			timeout1: null,
			timmer2: '',
			timeout2: '',
			deviceFound: false,
			searchTimmerId: 0,
			recharInter: null,
			onlineOrUsed: false,
			rechargeOrderUsed: false,
			firstTime: true
		}
	},
	methods: {
		...mapMutations([
			'setUserId', 'setDeviceId', 'setServiceId', 'setWriteServiceId', 'setNotifyServiceId', 'setCheckOnlineOrder',
			'setDeviceName', 'setDeviceImei', 'setCharging'
		]),

		getBattery() {

			let p = wx.getBatteryInfoSync()
			this.power = p.level








		},
		//充电时间到，不再去查询电量
		chargeTimeUp() {
			var that = this
			that.countdownColor = '#FF0000'
			clearInterval(that.batteryTimmer)
			that.setCharging(false);
			//跳转至首页
			uni.reLaunch({
				url: '../index/index'
			})
		},

		manulRecharge() {
			var that = this
			if (that.canTap) {
				that.canTap = false
				if (that.bleConnectedStatus) {
					//每次充电完，蓝牙都会关闭的
					that.sanAndConnectBle()
				} else {
					that.judgeBlueToothCanUse()

				}
			}
		},
		/**
		 * 复冲方法
		 */
		recharge() {
			var that = this
			uni.notifyBLECharacteristicValueChange({
				state: true,
				deviceId: that.deviceId,
				serviceId: that.serviceId,
				characteristicId: that.notifyServiceId,
				success(res) {
					//开启监听
					uni.writeBLECharacteristicValue({
						deviceId: that.deviceId,
						serviceId: that.serviceId,
						characteristicId: that.writeServiceId,
						value: String2Ab(that.checkOnlineOrder),
						success(res) {
							that.timmer1 = setInterval(function() {
								if (that.checkOnlineOrder.indexOf(that.responseOrder) != -1) {
									clearInterval(that.timmer1)
									clearTimeout(that.timeout1)
									that.onlineOrUsed = true
									//复冲
									that.writeChargeOrder2Device()

								}
							}, 200)
							//定时判断,如果5秒以后还不对，那么就是伪造的直接推出
							that.timeout1 = setTimeout(function() {
								if (that.checkOnlineOrder.indexOf(that.responseOrder) == -1) {
									if (!that.onlineOrUsed) {
										clearInterval(that.timmer1)
										uni.showModal({
											title: '',
											content: '蓝牙功能异常,请稍后重试',
											showCancel: false,
											success: (res) => {
												if (res.confirm) {
													uni.reLaunch({
														url: '../index/index'
													})

												}
											}
										})

									}
								}
							}, 5000);
						}
					})
				}
			})

		},

		writeChargeOrder2Device() {
			var that2 = this
			uni.request({
				url: that2.baseRequestUrl + '/order/getRechargeOrder?deviceImei=' + that2.deviceImei + '&userId=' + that2.userId,
				success: (res) => {
					if (res.data.code == 1) {
						let alradyOut = res.data.alradyOut
						if (alradyOut) {
							that2.setCharging(false)
							return uni.reLaunch({
								url: '../index/index'
							})
							
						}
						let recChargeOrder = res.data.order
						let retime = res.data.retime
						uni.writeBLECharacteristicValue({
							deviceId: that2.deviceId,
							serviceId: that2.serviceId,
							characteristicId: that2.writeServiceId,
							value: String2Ab(recChargeOrder),
							success(res) {
								//强制刷新子组件
								that2.countDownTime = retime
								that2.hackReset = false;
								that2.$nextTick(() => {
									that2.hackReset = true;
								})
								that2.timmer2 = setInterval(function() {
									if (recChargeOrder.indexOf(that2.responseOrder) != -1) {
										clearInterval(that2.timmer2)
										clearTimeout(that2.timeout2)
										that2.rechargeOrderUsed = true
										//下发Q06=1@
										uni.writeBLECharacteristicValue({
											deviceId: that2.deviceId,
											serviceId: that2.serviceId,
											characteristicId: that2.writeServiceId,
											value: String2Ab(that2.confirmOrder),
											success(res) {
												//关闭蓝牙连接
												that2.canTap = true
												that2.firstTime = false
											}
										})
									}
								}, 500)
								//定时判断,如果5秒以后还不对，那么就是伪造的直接推出
								that2.timeout2 = setTimeout(function() {
									if (recChargeOrder.indexOf(that2.responseOrder) === -1) {
										if (!that2.rechargeOrderUsed) {
											clearInterval(that2.timmer2)
											//关闭蓝牙连接
											uni.showModal({
												title: '',
												content: '蓝牙功能异常,请稍后重试',
												showCancel: false,
												success: (res) => {
													if (res.confirm) {
														that2.canTap = true
													}
												}
											})
										}
									}
								}, 5000);
							},
							fail(res) {}
						})

					} else {
						uni.showModal({
							title: '',
							content: res.data.message,
							showCancel: false,
							success: (res) => {
								if (res.confirm) {}
							}
						})
						return
					}
				},
				fail() {
					uni.showModal({
						title: '',
						content: '服务器开小差了,请稍后重试',
						showCancel: false,
						success: (res) => {
							if (res.confirm) {}
						}
					})
					return
				}
			})

		},
		need2index() {
			var that3 = this
			that3.canTap = false
			if (that3.bleConnectedStatus) {
				that3.sanAndConnectBle()
			} else {
				that3.judgeBlueToothCanUse()
			}
		},

		startQueryBattery() {
			var that = this
			that.batteryTimmer = setInterval(function() {
				that.getBattery()
			}, 5000)

		},
		sanAndConnectBle() {
			var that = this
			uni.startBluetoothDevicesDiscovery({
				//开启蓝牙搜索服务
				success: (res) => {
					uni.stopBluetoothDevicesDiscovery({
						success(res) {}
					})
					//1. 超时自动关闭蓝牙发现服务省电，10秒没有搜索到自己的蓝牙设备的话就直接关闭
					setTimeout(() => {
						uni.getBluetoothDevices({
							success(res) {
								//判断是否是自己的设备,只能通过name来判断
								var devicesArray = res.devices
								var deviceFound = false
								for (let i = 0; i < devicesArray.length; i++) {
									//2.1 如果设备名不存在直接跳出继续下次判断
									if (!devicesArray[i].name) {
										break
									}
									//2.2 查找自己的设备
									if (devicesArray[i].name.indexOf(that.deviceNamePrefix) != -1 && that.deviceImei.indexOf(devicesArray[
												i].name
											.substring(that.deviceNamePrefix.length)) != -1) {
										deviceFound = true
										that.setDeviceName(devicesArray[i].name)
										//2.3 封装实际的设备地址deviceId
										that.setDeviceId(devicesArray[i].deviceId)
										that.connectBle()
									}
									if ((i === devicesArray.length - 1) && (!deviceFound)) {
										uni.showModal({
											title: '',
											content: '未搜索到设备,请靠近设备后重试',
											showCancel: false,
											success: (res) => {
												if (res.confirm) {
													uni.redirectTo({
														url: '../index/index'
													})

												}
											}
										})
									}
								}
							}
						})
					}, 5000)
					//2. 会异步回调这个方法
					uni.onBluetoothDeviceFound((res) => {

					})
				},
				fail(res) {
					uni.showModal({
						title: '',
						content: '开启蓝牙搜索功能失败,请稍后重试',
						showCancel: false,
						success: (res) => {
							if (res.confirm) {
								uni.reLaunch({
									url: '../index/index'
								})

							}
						}
					})
				}
			})

		},
		connectBle() {
			var that = this
			//如果已经连接，那么久不创建连接了
			if (that.bleConnectedStatus) {
				uni.getBLEDeviceServices({
					deviceId: that.deviceId,
					success: (res) => {
						let bluetoothServiceArray = res.services
						for (let i = 0; i < bluetoothServiceArray.length; i++) {
							if (bluetoothServiceArray[i].uuid.toUpperCase().indexOf(that.serviceIdPrefix) != -1) {
								that.setServiceId(bluetoothServiceArray[i].uuid)
								uni.getBLEDeviceCharacteristics({
									deviceId: that.deviceId,
									serviceId: that.serviceId,
									success(res) {
										let characteristicArray = res.characteristics
										for (let i = 0; i < characteristicArray.length; i++) {
											if (characteristicArray[i].uuid.toUpperCase().indexOf(that.notifyIdPrefix) != -1) {
												that.setNotifyServiceId(characteristicArray[i].uuid)
											} else if (characteristicArray[i].uuid.toUpperCase().indexOf(that.writeIdPrefix) != -
												1) {
												that.setWriteServiceId(characteristicArray[i].uuid)
											}
											that.recharge()
										}
									},
									fail(res) {
										uni.showModal({
											title: '',
											content: '读取蓝牙特征失败,请稍后重试',
											showCancel: false,
											success: (res) => {
												if (res.confirm) {
													uni.redirectTo({
														url: '../index/index'
													})

												}
											}
										})
									}
								})
							}
						}
					},
					fail: (res) => {
						uni.showModal({
							title: '',
							content: '读取蓝牙服务失败,请稍后重试',
							showCancel: false,
							success: (res) => {
								if (res.confirm) {
									uni.reLaunch({
										url: '../index/index'
									})

								}
							}
						})
					}
				})

			} else {
				uni.createBLEConnection({
					deviceId: that.deviceId,
					success(res) {
						//查询所有蓝牙服务
						uni.getBLEDeviceServices({
							deviceId: that.deviceId,
							success: (res) => {
								let bluetoothServiceArray = res.services
								for (let i = 0; i < bluetoothServiceArray.length; i++) {
									if (bluetoothServiceArray[i].uuid.toUpperCase().indexOf(that.serviceIdPrefix) != -1) {
										that.setServiceId(bluetoothServiceArray[i].uuid)
										uni.getBLEDeviceCharacteristics({
											deviceId: that.deviceId,
											serviceId: that.serviceId,
											success(res) {
												let characteristicArray = res.characteristics
												for (let i = 0; i < characteristicArray.length; i++) {
													if (characteristicArray[i].uuid.toUpperCase().indexOf(that.notifyIdPrefix) != -1) {
														that.setNotifyServiceId(characteristicArray[i].uuid)
													} else if (characteristicArray[i].uuid.toUpperCase().indexOf(that.writeIdPrefix) != -
														1) {
														that.setWriteServiceId(characteristicArray[i].uuid)
													}
													that.recharge()
												}
											},
											fail(res) {
												uni.showModal({
													title: '',
													content: '读取蓝牙特征失败,请稍后重试',
													showCancel: false,
													success: (res) => {
														if (res.confirm) {
															uni.redirectTo({
																url: '../index/index'
															})

														}
													}
												})
											}
										})
									}
								}
							},
							fail: (res) => {
								uni.showModal({
									title: '',
									content: '读取蓝牙服务失败,请稍后重试',
									showCancel: false,
									success: (res) => {
										if (res.confirm) {
											uni.reLaunch({
												url: '../index/index'
											})

										}
									}
								})
							}
						})
					},
					fail(res) {
						//处理10003
						if (res.errCode === 10003) {
							//1. 断连接
							uni.closeBLEConnection({
								deviceId: that.deviceId,
								success(res) {
									console.log('10003中连接断开')
								}
							})
							uni.startBluetoothDevicesDiscovery({
								success: (res) => {
									var deviceFound1003 = false
									var timout1003 = setTimeout(function() {
										if (!deviceFound1003) {
											uni.stopBluetoothDevicesDiscovery({
												success(res) {}
											})
											uni.showModal({
												title: '',
												content: '请靠近设备后重试',
												showCancel: false,
												success: (res) => {
													if (res.confirm) {
														uni.reLaunch({
															url: '../index/index'
														})

													}
												}
											})

										}

									}, 10000)
									uni.onBluetoothDeviceFound((res) => {
										let devicesArray = res.devices
										for (let i = 0; i < devicesArray.length; i++) {
											if (!devicesArray[i].name) {
												break
											}
											//2.2 查找自己的设备
											if (devicesArray[i].name.indexOf(that.deviceNamePrefix) != -1 && that.deviceImei.indexOf(
													devicesArray[i].name
													.substring(that.deviceNamePrefix.length)) != -1) {
												deviceFound1003 = true
												//2.2.1 关闭定时器
												clearTimeout(timout1003)
												//2.2.2 关闭蓝牙搜索服务
												uni.stopBluetoothDevicesDiscovery({
													success(res) {}
												})
												//2.4 封装查询到的真实设备名
												that.connectBle()
											}
										}
									})
								},
							})
						} else {
							uni.showModal({
								title: '',
								content: '蓝牙连接失败,请检查蓝牙后重试',
								showCancel: false,
								success: (res) => {
									if (res.confirm) {
										uni.reLaunch({
											url: '../index/index'
										})

									}
								}
							})
						}

					}

				})
			}

		}
		/**
		 * 判断蓝牙是否可用,使用到了递归，只有开启蓝牙以后才进行蓝牙的初始化
		 */
		,
		judgeBlueToothCanUse() {
			var that = this
			//为了用户体验，延时1.5秒
			setTimeout(function() {
				if (that.phoneBlueToothCanUse) {
					//搜索并连接蓝牙
					that.sanAndConnectBle()
				} else {
					//手机蓝牙未打开
					uni.showModal({
						title: '',
						content: '请开启手机蓝牙功能',
						showCancel: false,
						success: (res) => {
							if (res.confirm) {
								that.judgeBlueToothCanUse()
							}
						}
					})

				}
			}, 1500);
		},
	},
	components: {
		uniCountdown
	},
	computed: {
		...mapState(['phoneBlueToothCanUse', 'baseRequestUrl', 'charging', 'deviceNamePrefix', 'notifyIdPrefix',
			'writeIdPrefix', 'serviceIdPrefix',
			'deviceImei', 'deviceName', 'deviceId',
			'serviceId', 'notifyServiceId', 'writeServiceId', 'bleConnectedStatus', 'responseOrder', 'checkOnlineOrder',
			'confirmOrder',
			'goodsObjectArray', 'commitGoodsArray', 'userId'
		]),
		//电源的
		powerClass() {
			if (this.power >= 60) {
				return 'font-size: 60rpx;color: #09BB07; margin-left: 10rpx;'
			} else {
				return 'font-size: 60rpx;color: #000000; margin-left: 10rpx;'
			}
		},
		btnMsg() {
			return this.canTap ? '申请复电' : '蓝牙连接中'
		}
	},
	onLoad() {
		//1. 页面加载时只执行一边查询电量信息
		this.startQueryBattery()
	},
	onShow() {
		//页面每次显示的时候去判断是否要跳转到首页
		this.need2index()
	}
}
